import React , {useEffect, useState} from "react";

import "./styles.css"
import { getStore, gen16CodeId, ObjectKeys, updStore, fetchEncryptStream, qryStore, compareJSON } from "../../../stores/Utils";
import { get_balance_params, get_balances_by_type, getall_subaccount, calc_balance, prepareBalanceComprobacion, getPrevBalance } from "../services/server_fetching";
import { useNavigate } from "react-router";
import { CloseModal } from "../../../components/DialogHRM";
import SelectAccounts from "./SelectAccounts";
import { SvgIcon_Cmpt, TabsHRM_Cmpt, MonthPicker_Cmpt } from "../../../components";
import { createPdf, ComprobacionBalancePdf } from "../services/print_services";
import { ResultSheet_Cmpt } from "..";
import { OpenToast } from "../../../components/Toast";

const Svg = SvgIcon_Cmpt();

const TabsHRM = TabsHRM_Cmpt();

const ResultSheet = ResultSheet_Cmpt();




const MonthPicker = MonthPicker_Cmpt();



const ParametrizeBalanceGeneral  = ({ data, accountForm }) => {
	const [obs, set_obs] = useState(0);
	const [view, set_view] = useState(0);
	useEffect(() => {
		getall_subaccount(getbalanceType);
		if(!view){
			set_view(1)
		}
	},[accountForm]);

	const getbalanceType =  () => {
		get_balances_by_type("balance_sheet","balance_params",getParams);
		get_balances_by_type("accounts_summary_by_months","accounts_summary_by_months", updObs);
		get_balances_by_type("result_sheet","result_params_value",updObs);
	}

	const getParams =  () => {
		get_balance_params(updObs);
	}

	
	const updObs =  () => {
		set_obs(gen16CodeId())
	}

	const calcBal =  () => {
		//calc_balance(getStore("balance_params")?.balanceId, getStore("result_params_value")?.balanceId, printBalance);
		printBalance();
	}

	let monthInit = (new Date(getStore("date_range_submayor")?.initDate)).getMonth();

	const printBalance =  () => {
		let mnt =  (new Date(getStore("date_range_submayor")?.initDate)).getMonth() + 1;

		let balance_general_param = getStore("balance_params");
		let result_params_value = getStore("result_params_value")
		
		let nnn = { general:{}, result:{}};
		getStore("balance_general_upd_flds").map(d4=>{
			if(!nnn.general[d4]){
				let gg = balance_general_param[d4];
				nnn.general[d4] = {};
				nnn.general[d4][gg] = {};
				nnn.general[d4][gg]["sub"] = {};
				nnn.general[d4][gg]["saldo"] = getPrevBalance(mnt,{account:gg});
				getStore("sub_account_group")?.[gg]?.map(suba=>{
					let subId = suba["sub_accountId"];
					nnn.general[d4][gg]["sub"][subId] = {};
					nnn.general[d4][gg]["sub"][subId]["saldo"] = getPrevBalance(mnt,{account:gg, subAccount: subId});
				});
			}
		})

		getStore("result_sheet_upd_flds").map(d4=>{
			if(!nnn.result[d4]){
				let gg = result_params_value[d4];
				nnn.result[d4] = {};
				nnn.result[d4][gg] = {};
				nnn.result[d4][gg]["sub"] = {};
				nnn.result[d4][gg]["saldo"] = getPrevBalance(mnt,{account:gg});
				getStore("sub_account_group")?.[gg]?.map(suba=>{
					let subId = suba["sub_accountId"];
					nnn.result[d4][gg]["sub"][subId] = {};
					nnn.result[d4][gg]["sub"][subId]["saldo"] = getPrevBalance(mnt,{account:gg, subAccount: subId});
				});
			}
		})

		//console.log("nnn", nnn);
		createPdf(nnn, getStore("balance_general_param"), getStore("balance_params"), getStore("result_sheet_params"), getStore("result_params_value"))
		set_obs(gen16CodeId())
	}



	const BalComp = async (td) => {
		let dataBC = await prepareBalanceComprobacion();
		ComprobacionBalancePdf(dataBC)
	}

	


	const navigate = useNavigate();

	const close =  (element) => {
		CloseModal({id:data.modalID});
		navigate({
			pathname: "accounting-ledger" ,
			search: `?cId=${element.comprobanteId}`
		});
	}
	

	const handlePickMonth =  ({initDate,lastDate}) => {

		updStore("date_range_submayor", {initDate,lastDate} );
		//get_filter_by_acc(accountForm,updObs);
		updObs();
	}


	
	


	return (
		<div style={{marginTop: 25}}>
			<div className="_dsplFlx ">
				<p className="title_collect">
					{ "Balances y Reportes"}
				</p>
				<div className="flexSpace"/>
				<div className="_dsplFlx"  >
					<div style={{marginTop: 6}}>
						<MonthPicker onSelect={handlePickMonth} initMonth={monthInit}/>
					</div>
				</div>
				<p className="group_title addBtn purple" onClick={calcBal}>
					Generar Balances
				</p>
				<p className="group_title addBtn purple" onClick={BalComp}>
					Balance de Comprobacion
				</p>
			</div>

			<TabsHRM  
				indexTab={view} 
				data={[
					{key:1,label:"General"}, 
					{key:2,label:"Resultado"}
				]} 
				updateIndex={set_view}  
			/>
			
			<OpacityView viewId={1} activeView={view}>
				<GeneralSheet />
			</OpacityView>

			<OpacityView viewId={2} activeView={view}>
				<ResultSheet />
			</OpacityView>


		</div>
	);
};

export default ParametrizeBalanceGeneral;


const OpacityView  = ({ viewId, activeView, children }) => {
	return(
		<div className={"opacityView"} style={{opacity : viewId=== activeView?1:0 }}>
			{viewId=== activeView?children:null}
		</div>
	)

}






const GeneralSheet  = ({ keyId,  }) => {
	const [obs, set_obs] = useState(0);
	let BgPmtz=getStore("balance_general_param")
	let flds=getStore("balance_general_upd_flds")

	
	const validateRes = () => {
		OpenToast({ text: "Los cambios en el Balance de General fueron guardados exitosamente ", timeout: 3500 });
		set_obs(gen16CodeId())
	}
	
	const save = async () => {
	
		if(getStore("balance_params")?.balanceId){
			let frm2Sv = {...getStore("balance_params")};
			frm2Sv.balanceId = gen16CodeId();
			let bdyq2 = {...getStore(qryStore())?.upd_balances};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId,
				balanceId: getStore("balance_params")?.balanceId
			};

			let diff = compareJSON(flds,getStore("balance_params"),getStore("balance_params" + "_bck") )

		
			bdyq2.data2update=diff.data;

			const res = fetchEncryptStream(bdyq2);
			const td = await res;
			if (td) {
				updStore("balance_params", frm2Sv);
				validateRes();
			}
		}
		else{
			let frm2Sv = {...getStore("balance_params")};
			frm2Sv.balanceId = gen16CodeId();
			frm2Sv.type = "balance_sheet";
			let bdyq2 = {...getStore(qryStore())?.add_balances};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			};
			bdyq2.form=frm2Sv;
			const res = fetchEncryptStream(bdyq2);
			const td = await res;
			if (td) {
				updStore("balance_params", frm2Sv);
				validateRes();
			}
		}
	}



	return (
		
			<div  style={{marginTop: 20, maxWidth: "90%"}}>
			<div className="_dsplFlx ">
				<p className="title_collect">
					{ "Balance General"}
				</p>
				<div className="flexSpace"/>
				<p className="group_title addBtn purple" onClick={save}>
					Guardar Parametros
				</p>

			</div>
			<div className="balance" style={{marginTop: 10}}>
			{BgPmtz && ObjectKeys(BgPmtz).map(r=>{
			
				return(
					<div key={r} style={{marginLeft: 12}}>
						
						<h5>{r}</h5>
						{!Array.isArray(BgPmtz[r])? 
							<div >
								{ObjectKeys(BgPmtz[r]).map(it=>{
									return (
										<div key={it}  style={{marginLeft: 27}}>
											<p>{it}</p>
											<div >
												{BgPmtz[r][it].map(suit=> <DetailAccount key={suit.key} keyId={suit.key} accountId={getStore("balance_params")?.[suit.key]} />)}
											</div>

										</div>
									)
								})}
							</div>
							:
							<div>
						{BgPmtz[r].map(it=> <DetailAccount keyId={it.key} key={it.key} accountId={getStore("balance_params")?.[it.key]}/>)}
						</div>}
					</div>
				)
			})}
		</div>
		</div>
	)
}






const DetailAccount  = ({ keyId,  }) => {
	const [edit, setEdit] = useState(false);
	const [show, setShow] = useState(false);
	const [obs, set_obs] = useState(0);
	let accountId = getStore("balance_params")?.[keyId];

	let data = getStore("account_ids")?.[accountId];
	let subAcc = data && getStore("sub_account_group")?.[data?.accountId];




	
	const updFrm = (f, v, k) => {
		let frmNm = "balance_params"
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};

	const selectItem = (v) => {
		updFrm(keyId, v);
		setEdit(false)
	}




	return (
		<div style={{marginLeft: 27}}>
			<div  className="_dsplFlx " >
			<div onClick={()=>setEdit(!edit)} className={`${edit?"editing":""}`}  style={{marginRight: 27}}>
				<span>
					{keyId}
				</span>
			</div>
			{edit?
			<div>
				<SelectAccounts
					handleClick={selectItem}
				/>
			</div>
			:
			<div>
				<div className={"bg_acc_code"}>
					{data?.code}
				</div>
				
			</div>
			
			}
			<div className="flexSpace"/>
			<div className="right_box _dsplFlx">
				<div className={"bg_acc_amount"} style={{marginRight: 15}}>
					
				</div>
				
				{ subAcc?.length > 0 ?
					<div className="icon" onClick={()=>setShow(!show)} style={{marginTop: 12}}>
						<Svg name={show?"key_arrow_up":"key_arrow_down"} color={"#828282"}/>
					</div>
				:null}
			</div>
		</div>
		{show && subAcc?.length && 
			<div >
				{ subAcc?.map(subacc=>{
					if(!subacc.subAccount){
						return null
					}
					return(
						<div 
							key={subacc.sub_accountId} 
							className="_dsplFlx account_item_box"  
							style={{marginLeft:"35px"}}
						>
							<div className="_dsplFlx" >
								<div className="code" >
									{subacc.subAccount}
								</div>
								<div className="name">
									{subacc.subAccountName}
								</div>
							</div>
						</div>
					)
				})}
			</div>
			}
	</div>
	)
}














